/* You can add global styles to this file, and also import other style files */
/* COLOR VARS */

:root {
  --chart-red: #c84b40;
  --chart-red-darker: #993931;
  --chart-blue: #41788e;

  --main-font-900: #212121;
  --main-font-800: #424242;
  --main-font-700: #757575;

  font-size: 16px;
}

/* ANIMATIONS */

@keyframes titlefadein {
  from {
    opacity: 0;
    /* margin-left: 10rem */
    transform: translateX(10rem);
  }
  to {
    opacity: 1;
    /* margin-left: 0rem; */
    transform: translateX(0rem);
  }
}

/* GENERAL STYLES */

* {
  box-sizing: border-box;
}

html {
  font-family: "Lora", serif;
  color: var(--main-font-900);
  line-height: 2;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
}

h3 {
  color: var(--chart-red-darker);
}

em {
  font-style: italic;
}

a {
  /* transition: .75s all; */
  text-decoration: none;
  border-bottom: 1px dashed var(--chart-red);
  color: var(--chart-red);
  cursor: pointer;
}

a:hover {
  color: var(--chart-red-darker);
  border-bottom: 1px solid var(--chart-red-darker);
}

ul {
  word-wrap: break-word;
}

.contained {
  width: 80%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.witchesgenrescycles,
.projectsonfilm {
  border-top: 1px dashed var(--main-font-900);
}

.dropcap {
  float: left;
  font-family: "Codystar";
  font-size: 6rem;
  line-height: 5rem;
  padding: 6px 8px 0 4px;
}

blockquote {
  border-left: 1px dashed var(--main-font-900);
  padding-left: 1rem;
}

@media screen and (max-width: 500px) {
  .contained {
    width: 90%;
  }
  blockquote {
    margin-left: 1rem;
    margin-right: 0.8rem;
  }
}

/* HEADER STYLES */

header {
  padding-top: 2rem;
  position: relative;
  /* top: 2rem; */
  /* right: 1rem; */
  /* left: 50%; */
}

nav {
  text-align: right;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

nav a {
  display: inline;
  margin-right: 2rem;
  color: var(--main-font-700);
  border-bottom: 1px solid var(--main-font-700);
}

nav a:hover {
  border-bottom: 1px dashed var(--chart-red-darker);
}

@media screen and (max-width: 900px) {
  nav a {
    padding: 5px 10px;
    margin-right: 0;
  }
  header {
    padding-top: 0.5rem;
  }
  nav {
    font-size: 0.9rem;
    line-height: 1.15;
    justify-content: space-around;
  }
}

/* HERO STYLES */

.hero {
  margin-top: 2rem;
}

.title-card {
  animation-name: titlefadein;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0.25s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

.title {
  font-family: "Codystar", sans-serif;
  font-weight: 300;
  font-size: 6rem;
  letter-spacing: 0.5rem;
  line-height: 1.15;
  margin: 0;
  padding: 1rem 0;
}

.title span {
  display: block;
}

.title span.mobilebreak {
  display: inline-block;
}

.byline {
  padding-left: 2rem;
  line-height: 1.8;
}

.byline span {
  font-style: italic;
}

.byline p {
  margin: 0;
}

.abstract-card {
  line-height: 1.8;
  margin-top: 5%;
}

.abstract-card p {
  padding-left: 2rem;
}

@media screen and (max-width: 950px) {
  .title {
    font-size: 4rem;
    overflow: hidden;
    text-align: center;
  }
  .byline {
    max-width: 75%;
    font-size: 0.9rem;
    margin: 0 auto;
  }
  .byline p {
    text-indent: -2rem;
  }
  .abstract-card p {
    padding-left: 0rem;
  }
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 4rem;
  }
}

@media screen and (max-width: 400px) {
  .title {
    font-size: 3rem;
  }
}

/* SCROLL SPECIFIC CSS */

#scroll {
  position: relative;
  border-top: 1px dashed var(--main-font-900);
  border-bottom: 1px dashed var(--main-font-900);
}

.mobileImg {
  display: none;
  visibility: hidden;
  max-height: 400px;
  height: auto;
  max-width: 100%;
  width: auto;
  margin: 0 auto;
  padding: 5px;
  border: 3px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem;
  cursor: pointer;
}

.scroll__text {
  /* IMPORTANT TO LAY ON TOP OF GRAPHIC */
  position: relative;

  padding: 0 1rem;
  max-width: 30rem;

  padding: 10% 0 0 5%;
  // transition: 0.3s filter;
}

.step {
  opacity: 0.3;
  // transition: all 1.5s;
}

.step.is-active {
  opacity: 1;
}

.scroll__graphic {
  position: absolute;
  top: 0;
  left: 0;
  bottom: auto;
  width: 100%;
  /* padding: 2rem; */
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.scroll__graphic.is-fixed {
  position: fixed;
}

.scroll__graphic.is-bottom {
  bottom: 0;
  top: auto;
}

.scroll__graphic .preview {
  position: absolute;
  right: 3rem;
  top: 3rem;
}

.preview {
  width: 50%;
  height: 50%;
  border: 1px dashed black;
  background-color: #7575;
}

.scroll__graphic .chart {
  position: absolute;
  /* right: 5%; */
  right: 1rem;
  top: 50%;
  -moz-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  // transition: 0.3s filter, 0.3s opacity;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

/* .scroll__graphic .chart img {
  border: 3px solid rgba(0, 0, 0, .2);
  padding-right: 10px;
  transition: .3s opacity,
              .3s filter;
} */

.blurry {
  filter: blur(10px) grayscale(50%);
  opacity: 0.5;
}

.driftImg {
  // transition: 1s all ease-out;
  opacity: 0;
  visibility: hidden;
  display: none;
  max-width: 95%;
  max-height: 40%;
  width: auto;
  height: auto;

  border: 3px solid rgba(0, 0, 0, 0.2);
  padding-right: 10px;
}

.driftImg.is-active {
  opacity: 1;
  visibility: visible;
  display: block;
}

.driftImg.imageplot {
  /* max-height: 30%;
  max-width: 30%;
  padding: 5px; */

  max-height: 60%;
  max-width: 80%;
  padding: 5px;

  background: rgba(0, 0, 0, 0.05);
}

.driftImg.boxOffice {
  max-height: 60%;
  max-width: 80%;
}

/* FULL OPACITY ON PRINT */
@media print {
  .step {
    opacity: 1;
  }
}

@media screen and (max-width: 800px) {
  .mobileImg {
    display: block;
    visibility: visible;
  }
  .scroll__graphic {
    display: none;
    visibility: hidden;
  }
  .scroll__text {
    max-width: 80%;
    padding: 0;
    margin: 0 auto;
  }
}

/* ACKNOWLEDGEMENTS */

.acknowledgements {
  margin: 5rem auto;
  max-width: 600px;
}

/* TOOLTIP CSS */

.tooltip {
  cursor: pointer;
  color: var(--chart-blue);
  border-bottom: 1px solid var(--chart-blue);
}

/* LIGHTBOX STYLES (CROSS YOUR FINGERS) */

.lum-lightbox-inner img {
  background: #fff;
  max-height: 100% !important;
}

.lum-lightbox {
  background: rgba(0, 0, 0, 0.6);
}
